"use client";
import Image from "next/image";
import React, { useState } from "react";
import worldWar from "../../../public/imgs/worldware.jpg";
import Button from "../Button/Button";
import { IoIosExpand } from "react-icons/io";
import { FaHeart } from "react-icons/fa";



export default function CardProduct() {
  const [addToCart, setAddToCart] = useState<boolean>(true);
  const [quantity, setQuantity] = useState<number>(0);
  const [addToWishList, setAddToWishList] = useState<boolean>(false);
 
  return (
    <>
      <figure className="flex flex-col relative overflow-hidden  group/parent hover:shadow-md items-center gap-4 p-4 bg-white rounded-md hover:border hover:border-sColor">
        <Image
          alt="name of product"
          src={worldWar}
          className="w-full h-[300px] rounded-md object-contain"
        />
        <h3 className="text-xl font-[500]">world war burger</h3>
        <p className="price text-sColor font-bold ">255 Egy</p>

        {/* Form to select size and quantity of item*/}
       <form dir="ltr" className="flex flex-col gap-4"  onSubmit={(e) => e.preventDefault()}>
          <div className="size">
            <h4 className="text-sColor font-bold">size</h4>
            <div className="flex justify-between items-center gap-3">
              <div className="size-item flex items-center  gap-2">
                <input
                  type="radio"
                  name="size"
                  id="single"
                  className="h-5 w-5 cursor-pointer appearance-none border-2 border-sColor rounded-full checked:bg-sColor checked:border-transparent transition-all duration-300 ease-in-out focus:ring-2 focus:ring-sColor"
                />
                <label htmlFor="single" className="text-lg">
                  single
                </label>
              </div>
              <div className="size-item flex items-center  gap-2"></div>
              <input
                type="radio"
                name="size"
                id="double"
                className="h-5 w-5 cursor-pointer appearance-none border-2 border-sColor rounded-full checked:bg-sColor checked:border-transparent transition-all duration-300 ease-in-out focus:ring-2 focus:ring-sColor"
              />
              <label htmlFor="double" className="text-lg">
                double
              </label>
            </div>
          </div>
          <div className="choice">
            <h4 className="text-sColor font-bold">choice</h4>
            <div className="flex justify-between items-center gap-3">
              <div className="size-item flex items-center  gap-2">
                <input
                  type="radio"
                  name="choice"
                  id="regualr"
                  className="h-5 w-5 cursor-pointer appearance-none border-2 border-sColor rounded-full checked:bg-sColor checked:border-transparent transition-all duration-300 ease-in-out focus:ring-2 focus:ring-sColor"
                />
                <label htmlFor="regualr" className="text-lg">
                  regualr
                </label>
              </div>
              <div className="size-item flex items-center  gap-2"></div>
              <input
                type="radio"
                name="choice"
                id="spicy"
                className="h-5 w-5 cursor-pointer appearance-none border-2 border-sColor rounded-full checked:bg-sColor checked:border-transparent transition-all duration-300 ease-in-out focus:ring-2 focus:ring-sColor"
              />
              <label htmlFor="spicy" className="text-lg">
                spicy
              </label>
            </div>
          </div>
          <textarea
            name="ملاحظات"
            id=""
            placeholder="اكتب هنا ملاحظاتك"
            className="w-full h-32 p-3 text-right text-gray-700 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-1 focus:ring-sColor focus:border-sColor placeholder-gray-400 shadow-sm transition-all duration-300 ease-in-out resize-none"
          ></textarea>
          <footer >
            {addToCart || quantity === 0 ? (
              <Button
                text="اضافة الي العربة"
                ariaLabel="اضافة الي العربة"
                className=" w-full cursor-pointer"
                onClick={() => {
                  setQuantity(1);
                  setAddToCart(!addToCart);
                }}
              />
            ) : (
              <div className="flex self-stretch rounded-md border border-gray-300 overflow-hidden">
                <button
                  aria-label="decreament "
                  className="bg-gray-300 px-4 py-2 "
                  onClick={() => setQuantity(quantity - 1)}
                >
                  {" "}
                  -{" "}
                </button>
                <span className="number-product grow flex items-center justify-center font-bold">
                  {quantity}
                </span>
                <button
                  aria-label="increament  "
                  className="bg-sColor text-whitep-2 px-4 py-2 "
                  onClick={() => setQuantity(quantity + 1)}
                >
                  {" "}
                  +{" "}
                </button>
              </div>
            )}
          </footer>
        </form>

        <div className="absLayer absolute top-2 -right-8  group-hover/parent:right-2 transition-[right] duration-1000  flex flex-col gap-4">
          <IoIosExpand className="text-3xl text-gray-700 font-bold cursor-pointer" />
          <FaHeart
            className={`text-3xl font-bold  cursor-pointer ${
              addToWishList ? "text-sColor" : "text-gray-700"
            } `}
            onClick={() => setAddToWishList(!addToWishList)}
          />
        </div>
      </figure>
    </>
  );
}
